import React, { useState, useEffect } from 'react';
import { getIncidentTypes, getUsers } from '../googleSheetsService';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow, Accordion, AccordionContent, AccordionItem, AccordionTrigger, Card, CardContent, CardHeader, CardTitle } from "./ui";

const DocumentationView = ({ accessToken }) => {
  const [incidentTypes, setIncidentTypes] = useState([]);
  const [users, setUsers] = useState([]); // New state for users
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [usersLoading, setUsersLoading] = useState(true); // New state for users loading
  const [usersError, setUsersError] = useState(null); // New state for users error

  useEffect(() => {
    const fetchIncidentTypes = async () => {
      try {
        const data = await getIncidentTypes(accessToken);
        setIncidentTypes(data);
      } catch (err) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    const fetchUsers = async () => { // New function to fetch users
      try {
        const data = await getUsers(accessToken);
        setUsers(data);
      } catch (err) {
        setUsersError(err.message);
      } finally {
        setUsersLoading(false);
      }
    };

    if (accessToken) {
      fetchIncidentTypes();
      fetchUsers(); // Call fetchUsers
    }
  }, [accessToken]);

  return (
    <div className="p-4">
      <h3 className="text-2xl font-bold mb-4">Documentaci de l'Aplicaci d'Incidncies</h3>
      <hr className="mb-4" />

      <h4 className="text-xl font-semibold mb-3">Rols d'Usuari i Permisos</h4>
      <p className="mb-4">
        Aquesta aplicaci utilitza un sistema de rols per gestionar l'accs a les diferents funcionalitats. 
        A continuaci es detallen els permisos de cada rol:
      </p>
      <h5 className="text-lg font-semibold mb-2">Usuaris Autoritzats</h5>
      {usersLoading && <p>Cargando usuarios...</p>}
      {usersError && <p className="text-red-500">Error al cargar usuarios: {usersError}</p>}
      {!usersLoading && !usersError && users.length === 0 && <p>No se encontraron usuarios autorizados.</p>}
      {!usersLoading && !usersError && users.length > 0 && (
        <Card className="mb-4">
          <CardContent className="p-0">
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Nom d'Usuari</TableHead>
                  <TableHead>Rol</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {users.map((user, index) => (
                  <TableRow key={index}>
                    <TableCell className="font-medium">{user.name}</TableCell>
                    <TableCell>{user.role}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </CardContent>
        </Card>
      )}

      <h4 className="text-xl font-semibold mb-3">Funcionalitat de cada Pestaa</h4>
      <p className="mb-4">A continuaci es descriu la funcionalitat principal de cada secci de l'aplicaci:</p>
      <ul className="list-disc list-inside mb-4">
        <li><strong>Pestaa de Incidencias (AddIncidentForm)</strong>: Permite a los usuarios registrar nuevas incidencias, especificando detalles como tipo, descripcin y duracin.</li>
        <li><strong>Pestaa de Resumen Anual (AnnualSummaryView)</strong>: Ofrece una vista consolidada de las incidencias registradas a lo largo del ao, con opciones de filtrado por usuario y tipo de incidencia.</li>
        <li><strong>Pestaa de Documentacin (DocumentationView)</strong>: Proporciona informacin detallada sobre el uso de la aplicacin, roles de usuario, tipos de incidencia y otras guas relevantes.</li>
      </ul>

      <Accordion type="single" collapsible className="w-full mb-4">
        <AccordionItem value="item-1">
          <AccordionTrigger><strong>1. Usuari</strong></AccordionTrigger>
          <AccordionContent>
            <p>El rol d'<strong>Usuari</strong> est pensat per al personal que ha de registrar les seves prpies incidncies.</p>
            <ul className="list-disc list-inside">
              <li><strong>Veure incidncies</strong>: Noms pot veure les seves prpies incidncies.</li>
              <li><strong>Crear incidncies</strong>: Pot crear noves incidncies per a si mateix.</li>
              <li><strong>Editar incidncies</strong>: Pot editar les seves prpies incidncies, sempre que no estiguin signades por la Direcci. Si una incidncia ja ha estat signada (per l'usuari o la direcci) i s'edita, l'original se marcar como a "modificada" i se'n crear una de nova amb els canvis.</li>
              <li><strong>Signar incidncies</strong>: Pot signar les seves prpies incidncies si encara no han estat signades per ning. La signatura d'usuari s un pas de validaci inicial.</li>
              <li><strong>Resum Anual</strong>: Pot veure un resum anual de les seves prpies incidncies.</li>
              <li><strong>Incidncies Modificades</strong>: Pot veure un llistat de les seves incidncies que han estat modificades despus de ser signades.</li>
            </ul>
          </AccordionContent>
        </AccordionItem>

        <AccordionItem value="item-2">
          <AccordionTrigger><strong>2. Gestor</strong></AccordionTrigger>
          <AccordionContent>
            <p>El rol de <strong>Gestor</strong> t permisos per supervisar les incidncies de tots els usuaris, per no pot validar-les finalment.</p>
            <ul className="list-disc list-inside">
              <li><strong>Veure incidncies</strong>: Pot veure les incidncies de <strong>tots</strong> els usuaris. Pot filtrar per usuari o per any.</li>
              <li><strong>Crear i Editar incidncies</strong>: Pot crear i editar incidncies per a qualsevol usuari. S'aplica la mateixa lgica de "modificaci" si edita una incidncia ya signada.</li>
              <li><strong>Signar incidncies</strong>: <strong>No pot</strong> signar incidncies. La signatura est reservada als Usuaris i a la Direcci.</li>
              <li><strong>Resum Anual</strong>: Pot veure un resum anual de les incidncies de tots els usuaris.</li>
              <li><strong>Incidncies Modificades</strong>: Pot veure el llistat de <strong>totes</strong> les incidncies que han estat modificades despus de ser signades.</li>
            </ul>
          </AccordionContent>
        </AccordionItem>

        <AccordionItem value="item-3">
          <AccordionTrigger><strong>3. Direcci</strong></AccordionTrigger>
          <AccordionContent>
            <p>El rol de <strong>Direcci</strong> t el control total de l'aplicaci, incloent la validaci final de les incidncies.</p>
            <ul className="list-disc list-inside">
              <li><strong>Veure incidncies</strong>: Pot veure les incidncies de <strong>tots</strong> els usuaris. Pot filtrar per usuari o per any.</li>
              <li><strong>Crear i Editar incidncies</strong>: Pot crear i editar incidncies per a qualsevol usuari. Si edita una incidncia signada per la direcci, l'original se marcar como a "modificada" i se'n crear una de nova.</li>
              <li><strong>Signar incidncies</strong>: Pot signar qualsevol incidncia com a validaci final (`Signatura Direcci`). Aquesta signatura bloqueja l'edici per part dels Usuaris.</li>
              <li><strong>Resum Anual</strong>: Pot veure un resum anual de les incidncies de tots els usuaris.</li>
              <li><strong>Incidncies Modificades</strong>: Pot veure el llistat de <strong>totes</strong> les incidncies que han estat modificades despus de ser signades.</li>
            </ul>
          </AccordionContent>
        </AccordionItem>
      </Accordion>

      <h4 className="text-xl font-semibold mb-3">Tipus d'Incidncia i Documentaci Associada</h4>
      {loading && <p>Cargando tipos de incidencia...</p>}
      {error && <p className="text-red-500">Error: {error}</p>}
      {!loading && !error && incidentTypes.length === 0 && <p>No se encontraron tipos de incidencia.</p>}
      {!loading && !error && incidentTypes.length > 0 && (
        <Card>
          <CardContent className="p-0">
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead className="w-[20%]">Nom del Tipus</TableHead>
                  <TableHead className="w-[40%]">Descripci</TableHead>
                  <TableHead className="w-[40%]">Notes sobre la Durada</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {incidentTypes.map((type, index) => (
                  <TableRow key={index}>
                    <TableCell className="font-medium">{type.type}</TableCell>
                    <TableCell>{type.description}</TableCell>
                    <TableCell>
                      {type.durationUnit === 'H' && (
                        <p>Les incidncies sn noms d'un dia. Cal introduir la data d'inici i les hores d'inici i final.</p>
                      )}
                      {type.durationUnit === 'D' && (
                        <p>Les incidncies poden ser d'un o ms dies. Cal introduir la data d'inici i la data de final (si s diferent). Les hores no sn rellevants per al clcul de la durada.</p>
                      )}
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </CardContent>
        </Card>
      )}
    </div>
  );
};

export default DocumentationView;